      info: {
        name: "汉庭酒店",
        address: "红军路187号",
        phone: "15070090826",
        type: "宾馆酒店",
        linkUser: "茶园派出所--李健",
      },

      menuList: [
        {
          icon: "#icon-yuangong",
          name: "从业人员",
          page: "",
        },
        {
