          page: "",
          badge: "4",
        },
        {
          icon: "#icon-lvdianyingyong",
          name: "旅客登记",
          page: "/register/rooms",
        },
      ],
    };
  },
  created() {
    this.dictionaries();

    this.openhome();
  },



    computed: {
    staticUrl() {
      //静态资源地址
      return window.staticUrl;
    },
  },
  methods: {

    bandpassword() {
      this.$router.push({
        path: "/homepage",
        query: { status: 1 },
      });
    },

    construction(){
     this.$api.msg("建设中");

    },

    openUser() {
      this.userBox = true;
    },

    logout() {
      store.dispatch("user/resetToken").then(() => {
        location.reload();
      });
    },

    openhome() {
      let parans = {};
      openhome(parans)
        .then((res) => {
          if (res.code == 0) {
            this.info = res.data;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },

    toDetail(path) {
      if (path == "/register/rooms") {
        this.$router.push({
          path: path,
        });
      } else {
        this.$api.msg("建设中");
      }
    },
    dictionaries() {
      dictionaries({}).then((res) => {
        // if(res.code==0){
        //    this.list = res.list;
        // }else{

        //     this.$api.msg('错误错误错误错误错误错误错误错误')
        // }

        console.log(res);
      });
    },
  },
};
